import { router } from '../router/routes'
import Vue from 'vue'

function addFilters(filters){
  Object.keys(filters).forEach(function(key) {
    Vue.filter(key, filters[key]);
  });
}

function dropQueryParamByReplaceToLocation(location){
  if(!isEmpty(router.history.current.query)) {
    router.replace(location)
  }
}
function isEmpty(obj) {
  return Object.keys(obj).length === 0;
}

function getRouterQueryParam(key){
  const routerQuery = router.app._route.query;
  // eslint-disable-next-line no-prototype-builtins
  if(routerQuery.hasOwnProperty(key)){
    return routerQuery[key];
  }
}
function urlWithQueryString(url, ...queryArgs) {
  const first = 0;
  queryArgs.forEach((arg, index) => {
    if (Object.values(arg) && Object.values(arg)[first]) {
      url += (index === first ? '?' : '&');
      url += Object.keys(arg)[first] + '=' + Object.values(arg)[first];
    }
  });
  return url;
}

export function isDevMode() {
  return process.env.NODE_ENV === 'development'
}

